#header {
    position: fixed;
    left: 0;
    top: 0;
    width: 260px;
    height: 100%;
    z-index: 10000;
    background-color: #111;
}

#main {
    max-width: 2000px;
    height: calc(100vh - 80px);
    min-height: 100vh;
    margin: 0 auto;
    padding-left: 260px;
    background-color: #000;
    position: relative;
}

#footer {
    text-align: center;
    background-color: #000;
    padding: 20px 20px 20px 260px;
}

#best,
#racipes,
#greentea,
#barleytea,
#jasminetea,
#rooibostea,
#pepperminttea,
#earlgraytea,
#lemontea,
#citrontea {
    padding: 40px;

    h2 {
        margin-bottom: 0.6rem;
        font-size: 1.25rem;
    }
}

@media (max-width: 960px) {
    #header {
        left: -260px;
    }

    #main {
        padding-left: 0;
        margin-top: 80px;
    }

    #best,
    #racipes,
    #greentea,
    #barleytea,
    #jasminetea,
    #rooibostea,
    #pepperminttea,
    #earlgraytea,
    #lemontea,
    #citrontea {
        padding: 20px;

        h2 {
            margin-bottom: 0.6rem;
            font-size: 1.25rem;
        }
    }
}